.btn{

    &_primary{
        border: none;
        border-radius: .5rem;
        background-color: $color_primary;
        font-size: 2rem;
        font-weight: 900;
        letter-spacing: .1rem;
        color: $color_grey-dark;
        outline: none;
        cursor: pointer;
        
        
        &--del{
            width: max-content;
            background-color: $color_red;
        }

        &--add{
            width: max-content;
            background-color: $color_green;
        }

        @include respond('sm-tab'){
            font-size: 3rem;
        }

        @include respond('med-phone'){
            font-size: 1.5rem;
        }
        
            &-center{
                display: block;
                margin: .5rem;
                padding: .5rem;
                border-radius: .25rem;
                background-color: white;
                transition: all .2s ease;
                white-space: nowrap;

                &--del{
                    &:hover{
                        color: $color_warning;
                    }
                }

                @include respond('sm-phone'){
                    padding: 0 1rem;
                }
            }
        
            &:hover &-center{
                transform: translateY(-.2rem) scale(1.015);
                box-shadow: .5px 2px 8px .1px $color_btn_shadow;
                white-space: nowrap;
            }
        
            &:active &-center{
                transform: translateY(-.05rem) scale(.995);
                box-shadow: 1px 2px 2px .2px $color_btn_shadow;
                white-space: nowrap;
            }
    }

    &_secondary{
        border: none;
        border-radius: .5rem;
        background-color: $color_primary;
        font-size: 1.5rem;
        font-weight: 900;
        letter-spacing: .1rem;
        color: $color_grey-dark;
        outline: none;
        cursor: pointer;

        &_selected{
            background-color: $color_selected;
        }
        
            &-center{
                display: block;
                margin: .5rem;
                padding: .25rem;
                border-radius: .25rem;
                background-color: white;
                transition: all .2s ease;
            }
        
            &:hover &-center{
                transform: translateY(-.2rem) scale(1.015);
                box-shadow: .5px 2px 8px .1px $color_btn_shadow;
            }
        
            &:active &-center{
                transform: translateY(-.05rem) scale(.995);
                box-shadow: 1px 2px 2px .2px $color_btn_shadow;
            }
    }



}

