.H{
    &_primary{
        font-family: 'Kanit', sans-serif;
        font-size: 7rem;
        font-weight: 800i;
        letter-spacing: .2rem;
        background: -webkit-linear-gradient(0deg, $color_primary, $color_primary-dark);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;

        @include respond('sm-phone'){
            font-size: 4rem;
        }

    }

    &_secondary{
        font-family: 'Kanit', sans-serif;
        font-size: 6rem;
        font-weight: 800i;
        letter-spacing: .2rem;
        background: -webkit-linear-gradient(0deg, $color_grey-dark, $color_black);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;

        &-sub{
            font-size: 4rem;
        }

        &-white{
            font-family: 'Kanit', sans-serif;
            font-size: 4rem;
            font-weight: 800i;
            letter-spacing: .2rem;
            color: rgb(238, 238, 238);

            @include respond(lg-phone){
                font-size: 1.5rem;
            }
    
            @include respond(sm-tab){
                font-size: 3rem;
            }
    
            @include respond(lg-tab){
                font-size: 4rem;
            }
        }
        
        @include respond(sm-phone){
            font-size: 3.5rem;
        }

        @include respond(lg-phone){
            font-size: 5rem;
        }

        @include respond(lg-tab){
            font-size: 5.5rem;
        }
        
    }

    &_tertiary{
        font-family: 'Kanit', sans-serif;
        font-size: 2.0rem;
        font-weight: 800;
        letter-spacing: .2rem;
        color: $color_primary;


    }
}

.P{
    font-size: 1.5rem;
    color: $color_black;
    font-weight: 400;
    letter-spacing: .1rem;
}

.data{
    font-size: 1.2rem;
    color: $color_black;
    font-weight: 400;
    letter-spacing: .1rem;
}

.form{

    &_input{

    }

    &_label{
        font-size: 1.5rem;
        font-weight: 700;
        color: $color_grey_dark;
    }

    &_close{
        font-family: 'Kanit', sans-serif;
        font-size: 2rem;
        font-weight: 800i;
        color: $color_grey_dark;

        @include respond(sm-phone){
       
        }

        @include respond(lg-phone){
            
        }

        @include respond(lg-tab){
            font-size: 3rem;
        }




    }
}

.gauge{
    font-size: 3rem;
    font-weight: 800i;
    color: $color_white;

    &_label{
        font-family: 'Kanit', sans-serif;
        text-transform: uppercase;
        font-size: 2rem;
        font-weight: 800;
        color: $color_grey_dark;
    }
}

.menu{
    color: $color_grey_dark;
}

.error{
    color: red;
    font-size: 1.5rem;
    text-transform: uppercase;
    padding: 2rem;
}